@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
.SideBar {
  width: 100%;
  height: 100vh;
  background-color: #181818;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
  z-index: 11;
}
.profileImage {
  width: 70%;
  border-radius: 50%;
}
.navigation {
  margin-top: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

}
.navigationItem {
  cursor: pointer;
}
.navigationText {
  color: #909096;
  font-size: 17px;
  letter-spacing: 0.5px;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  transition: 0.3s ease;
  width: 100%;
  height: 100%;
padding: 15px 0;}

.navigationText:hover {
  color: #64FFDA !important;
}
.svg {
  width: 50px;
  height: 50px;
}
.social {
  margin-top: 100px;
  width: 40%;
  display: flex;
  justify-content: space-between;
}
.iconContainer {
  cursor: pointer;
}
.navigationItem{
    display: flex;
    justify-content: center;
    border-top: 1px solid #9090963c;
    width: 90%;
}
.navigationItem:last-child{
    border-bottom: 1px solid #9090963c;
}
@media only screen and (max-width: 1000px) {
  .SideBar{
   display: none;
  }
}