@import url("https://fonts.googleapis.com/css2?family=Cairo&family=Roboto&family=Rock+Salt&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap");



.Section{
  width: 100%;
  height: 100vh;
  background-color: #1d1d1d;
  overflow: hidden;
}
.AboutSection {

  position: relative;
  padding: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.title {
  color: #64FFDA;
  font-family: "Titillium Web", sans-serif;
  letter-spacing: 5px;
  font-size:4vw;
  text-transform: uppercase;
}
.aboutParagraph {
  color: #fff;
  font-family: "Roboto", sans-serif;
  line-height: 2vw;
}
.aboutInfos {
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 10;

}
.aboutLink {
  color: #64FFDA;
  font-size: 17px;
  cursor: pointer;
}

.title {
  animation: glitch 1s linear infinite;
}

@keyframes glitch {
  2%,
  64% {
    transform: translate(2px, 0) skew(0deg);
  }
  4%,
  60% {
    transform: translate(-2px, 0) skew(0deg);
  }
  62% {
    transform: translate(0, 0) skew(5deg);
  }
}

.title:before,
.title:after {
  content: attr(title);
  position: absolute;
  left: 0;
}

.title:before {
  animation: glitchTop 1s linear infinite;
  clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
}

@keyframes glitchTop {
  2%,
  64% {
    transform: translate(2px, -2px);
  }
  4%,
  60% {
    transform: translate(-2px, 2px);
  }
  62% {
    transform: translate(13px, -1px) skew(-13deg);
  }
}

.title:after {
  animation: glitchBotom 1.5s linear infinite;
  clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
  -webkit-clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
}

@keyframes glitchBotom {
  2%,
  64% {
    transform: translate(-2px, 0);
  }
  4%,
  60% {
    transform: translate(-2px, 0);
  }
  62% {
    transform: translate(-22px, 5px) skew(21deg);
  }
}
.loading {
  font-family: "Arial Black", "Arial Bold", Gadget, sans-serif;
  text-transform: uppercase;
  max-width: 350px;
  text-align: center;
  line-height: 50px;
  margin-top: 30px;
  transform: translateY(-50%);
  cursor: pointer;
}

.loading span {
  position: relative;
  z-index: 999;
  color: #fff;
}
.loading:before {
  content: "";
  background: #0c9d7b;
  height: 36px;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  cursor: pointer;

  animation: 2s loadingBefore infinite ease-in-out;
}

@keyframes loadingBefore {
  0% {
    transform: translateX(-14px);
  }
  50% {
    transform: translateX(14px);
  }
  100% {
    transform: translateX(-14px);
  }
}

.loading:after {
  content: "";
  background: #fff;
  width: 14px;
  height: 60px;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  opacity: 0.5;

  animation: 2s loadingAfter infinite ease-in-out;
}

@keyframes loadingAfter {
  0% {
    transform: translateX(-50px);
  }
  50% {
    transform: translateX(50px);
  }
  100% {
    transform: translateX(-50px);
  }
}
.skills{
  z-index: 10;

}


@media only screen and (max-width: 1000px) {
  .AboutSection{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
  }
  .aboutInfos{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 30px;
  }
  .aboutParagraph{
    line-height: 4vh;
    text-align: center;
    font-size: 13px;
  }
  canvas{

    margin-top: -150px;
  }
  .title{
    font-size: 6vw;
  }
}
.btnLink{
  text-decoration: none;
  width:350px !important;
  height: 70px;
  margin: 0;
  padding: 0;
  display: inline-block;
}