@import url("https://fonts.googleapis.com/css2?family=Cairo&family=Roboto&family=Rock+Salt&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap");
.Section {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: #1d1d1d;
  overflow: hidden;
}
.SkillsSection {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 40px;
  width: 100%;
  height: 100vh;
  overflow: auto;
    overflow-x: hidden;
}
.SkillsInfos {
  width: 80%;
  margin-top: 30%;
  z-index: 10;
}
.SkillsInfos h1 {
  font-family: "Titillium Web", sans-serif;
  margin: 0;
  font-size: 60px !important;
  color: #64ffda;
}
.SkillsInfos p {
  color: rgba(255, 255, 255, 0.826);
  font-family: "Titillium Web", sans-serif;
  font-size: 17px;
}

.SkillsCharts {
  width: 80%;
  z-index: 10;
  margin-top: 10%;
}
.skill {
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.skillTitle {
  font-family: "Titillium Web", sans-serif;
  color: #64ffda;
}
.skillLabel {
  color: #fff;
  font-family: "Titillium Web", sans-serif;
  font-size: 18px;
}
.skillChart {
  width: 80%;
  height: 3px;
  background-color: #434343;
}
.chart {
  width: 100%;
  height: 3px;
}

@media only screen and (max-width: 1000px) {
  .SkillsSection {
    grid-template-columns: 1fr;
  }
  .SkillsInfos {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 10%;
  }
  .SkillsCharts {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 50px;
  }
  .skillChart {
    width: 100% !important;
  }
  .SkillsInfos p {
    text-align: center;
  }
  .SkillsInfos h1 {
    font-size: 30px !important;
  }
}
