@import url("https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap");
body {
  overflow: hidden;
}
.HomeSection {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  overflow: hidden;
  background-color: #1d1d1d;
  height: 100vh;
}
.homeInfos {
  text-align: left;
  z-index: 10;
}
.homeInfos h3 {
  font-size: 1.1vw;
  color: #64ffda;
  font-family: "Titillium Web", sans-serif;
}

.homeInfos h2 {
  font-family: "Titillium Web", sans-serif;
  font-size: 3vw;
  margin: 0;
  color: rgba(255, 255, 255, 0.675);
}
.homeInfos .title2 {
  font-family: "Titillium Web", sans-serif;
  font-size: 60px;
  margin: 0;
  color: #fff;
}
.homeInfos p {
  max-width: 40%;
  font-size: 17px;
  font-family: "Titillium Web", sans-serif;
  color: rgba(255, 255, 255, 0.675);
}
.btn {
  width: 250px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #64ffda;
  cursor: pointer;
  border-radius: 5px;
}
.btn h4 {
  color: #64ffda;
  font-family: "Titillium Web", sans-serif;
}

.circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.circles li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.2);
  animation: animate 25s linear infinite;
  bottom: -150px;
}

.circles li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circles li:nth-child(3) {
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circles li:nth-child(5) {
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.circles li:nth-child(6) {
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7) {
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.circles li:nth-child(8) {
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circles li:nth-child(9) {
  left: 40%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circles li:nth-child(10) {
  left: 30%;
  width: 40px;
  height: 40px;
  animation-delay: 5s;
  animation-duration: 11s;
}
.circles li:nth-child(11) {
  left: 60%;
  width: 50px;
  height: 50px;
  animation-delay: 10s;
  animation-duration: 11s;
}
.circles li:nth-child(12) {
  left: 40%;
  width: 700px;
  height: 070px;
  animation-delay: 1s;
  animation-duration: 11s;
}
.circles li:nth-child(13) {
  left: 70%;
  width: 80px;
  height: 80px;
  animation-delay: 7s;
  animation-duration: 11s;
}

@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 0;
  }

  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 0;
    border-radius: 50%;
  }
}
.start {
  margin: 0;
}
.end1 {
  margin-right: 10px !important;
}
.end1,
.middle1,
.middle2 {
  display: inline-block;
  overflow: hidden;
  font-size: 75px;
  margin: 0;
  color: #fff;
}
.end2 {
  position: absolute;
  color: #ff6a00;
}
.middle1,
.middle2 {
  color: #cfcfcf;
}
.middle1 {
  animation: ani1 5s infinite;
  animation-delay: -3s;
  max-width: 0;
}
.middle2 {
  animation: ani2 5s infinite;
  animation-delay: -3s;
  max-width: 0;
}

@keyframes ani1 {
  0% {
    max-width: 500px;
  }

  25% {
    max-width: 0;
  }

  75% {
    max-width: 0;
  }

  90% {
    max-width: 500px;
  }

  100% {
    max-width: 500px;
  }
}

@keyframes ani2 {
  0% {
    max-width: 0;
  }

  25% {
    max-width: 0;
  }

  50% {
    max-width: 500px;
  }

  75% {
    max-width: 0;
  }

  100% {
    max-width: 0;
  }
}
@media only screen and (max-width: 1000px) {
  .homeInfos {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .homeInfos{
    text-align: center;
  }
  .homeInfos p {
    max-width: 70%;
    font-size: 15px;
   
  }
  .homeInfos .title2 {
font-size: 30px;
  }
  .homeInfos h2 {
font-size: 20px;
  }
  .homeInfos h3 {
    font-size:3.5vw;
 
  }
  
}
.btnLink{
  text-decoration: none;
  width: 250px;
  height: 50px;
  margin: 0;
  padding: 0;
  display: inline-block;
}