@import url("https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap");

* {
  box-sizing: border-box;
}
.Section {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: #1d1d1d;
  overflow: hidden;
}
.ProjectsContainer {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  padding: 50px 20px;
  overflow: auto;
    overflow-x: hidden;
}
.WorkSection {
  scroll-behavior: smooth;
  overflow-x: hidden !important;
  padding: 50px;
}
.projectReverse {
  display: flex;
  flex-direction: row;
  margin-bottom: 60px;
  width: 100%;
  z-index: 55 !important;
}
.project {
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: 0;
  width: 100%;
  z-index: 55 !important;
}
.imageContainer {
  position: relative;
  cursor: pointer;
  width: 420px;
  height: 330px;
  z-index: 2;
  border-radius: 5px;

}

.ProjectImage {
  width: 420px;
  height: 330px;
  cursor: pointer;
  border-radius: 5px;
}

.imageContainer::before {
  width: 100%;
  height: 100%;
  background-color: #1d1d1daf;
  content: "";
  z-index: 10;
  display: block;
  position: absolute;
  transition: 0.4s ease;
}
.imageContainer:hover::before {
  background-color: transparent !important;
}

.projectInfos {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.projectInfos h1 {
  margin: 0;
  text-align: right;
  font-size: 25px;
}

.PojectDescription {
  background-color: #226c5b;
  text-align: right;
  width: 120%;
  margin-left: -20%;
  z-index: 20;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 20px;
  transition: 0.3s ease;
}
.PojectDescription:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.487);
}
.PojectDescription h3 {
  margin: 0;
}
.projectOverView {
  color: rgb(214, 214, 214);
  font-family: "Titillium Web", sans-serif;
}
.keyword {
  color: #fff;
  margin: 0;
  font-size: 15px;
  text-transform: capitalize;
  text-align: right;
  font-family: "Titillium Web", sans-serif;
}
.projectTitle {
  color: #64ffda;
  font-size: 17px !important;
  margin-bottom: 10px !important;
  font-family: "Titillium Web", sans-serif;
}
.ProjectName {
  color: #fff;
  font-size: 25px !important;
  margin-bottom: 10px !important;
  font-family: "Titillium Web", sans-serif;
}
.iconContainer {
  margin-top: 20px;
}


.reverse {
  text-align: left !important;
  align-items: flex-start;
}
.reverseMargin {
  margin-left: 0 !important;
  margin-right: -20%;
}

@media only screen and (max-width: 1000px) {
  .projectInfos{
    padding-right: 5px;
  }
  .ProjectsContainer{
    grid-template-columns: 1fr;
  }
  .ProjectImage {
    width:100%;
    height: 100%;
    position: absolute;
   
  }
  .imageContainer {
    width:100%;
    height: 100%;
    position: absolute;
   
  }
  .PojectDescription {
    background-color: #226c5b;
    text-align: right;
    width: 95%;
    margin-left: 0 !important;
    z-index: 20;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 20px;
    transition: 0.3s ease;
  }
  .project{
    position: relative;
  }
  .projectTitle,.ProjectName,.keyword{
    z-index: 10;
  }
  .PojectDescription{
    background-color: transparent;
  }
  .imageContainer::before {
  background-color: rgba(0, 0, 0, 0.778);
  }
  .PojectDescription:hover {
  box-shadow: none;
  }
  .projectOverView{
    font-size: 14px;
  }
  .keyword{
    font-size: 15px;
  }

}
