.Section {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: #1d1d1d;
  overflow: hidden;
}
.Education {
  padding-top: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  z-index: 10;
  width: 100%;
  height: 100vh;
  overflow: auto;
  overflow-x: hidden;
  padding-bottom: 30px;
}

p {
  font-weight: 300;
}

strong {
  font-weight: 600;
}
h1 {
  letter-spacing: 1.5px;
  color: #fff;
  font-weight: 400;
  font-size: 2.0em;
  font-family: "Titillium Web", sans-serif;
  margin:0px;
  padding: 0;
}
#timeline-content {
  margin-top: 10px;
  text-align: center;
  z-index: 10;
}
.timeline {
  border-left: 4px solid #64ffda;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  background: rgba(255, 255, 255, 0.03);
  color: rgba(255, 255, 255, 0.8);
  margin: 50px auto;
  letter-spacing: 0.5px;
  position: relative;
  line-height: 1.4em;
  font-size: 1.03em;
  padding: 50px;
  list-style: none;
  text-align: left;
  font-weight: 100;
  max-width: 40%;
}
.timeline h1 {
  letter-spacing: 1.5px;
  font-weight: 100;
  font-size: 1.4em;
}
.timeline h2,
.timeline h3 {
  letter-spacing: 1.5px;
  font-weight: 400;
  font-size: 1.4em;
}
.timeline .event {
  border-bottom: 1px dashed rgba(255, 255, 255, 0.1);
  padding-bottom: 25px;
  margin-bottom: 50px;
  position: relative;
}
.timeline .event:last-of-type {
  padding-bottom: 0;
  margin-bottom: 0;
  border: none;
}
.timeline .event:before,
.timeline .event:after {
  position: absolute;
  display: block;
  top: 0;
}
.timeline .event:before {
  left: -217.5px;
  color: rgba(255, 255, 255, 0.4);
  content: attr(data-date);
  text-align: right;
  font-weight: 100;
  font-size: 0.9em;
  min-width: 120px;
}
.timeline .event:after {
  box-shadow: 0 0 0 4px #64ffda;
  left: -57.85px;
  background: #313534;
  border-radius: 50%;
  height: 11px;
  width: 11px;
  top: 5px;
  content: "";
}
@media only screen and (max-width: 1000px) {
  .timeline .event:after {
    width: 5px;
    height: 5px;
  }
  .Education {
    display: grid;
    grid-template-columns: 1fr;
  }
  .timeline {
    max-width: 75%;
    font-size: 15px;
    padding: 20px;

  }
  .timeline .event:before {
    font-size: 12px;
  }
  #timeline-content {
    margin-top:0px;
  
  }
  .timeline .event {
    padding-bottom:5px;
    margin-bottom: 5px;
  }
  h1 {
  
    font-size: 1.5em;
  
    margin:0px;
    padding: 0;
  }
}
